import React, { useState, useEffect } from 'react'
import RecipeNameRow from '../RecipeNameRow'
import RecipeDirectionsRow from '../RecipeDirectionsRow'
import RecipeIngredientsRow from '../RecipeIngredientsRow'

export default function RecipeTable() {

  const [recipeName, setRecipeName] = useState('')
  const [recipeDirections, setRecipeDirections] = useState('')
  const [recipeIngredients, setRecipeIngredients] = useState('')

  useEffect(() => {
    document.title = recipeName
  })

  function handleRecipeNameChange(e) {
    setRecipeName(e.target.value)
  }

  function handleRecipeDirectionsChange(e) {
    setRecipeDirections(e.target.value)
  }

  function handleRecipeIngredientsChange(e) {
    setRecipeIngredients(e.target.value)
  }

  return (
    <table>
      <thead></thead>
      <tbody>
        <tr>
          <td>
            <table>
              <thead>
                <tr>
                  <th><h1>Recipe</h1></th>
                </tr>
              </thead>
              <tbody>
                <RecipeNameRow 
                  type='text'
                  label='Name'
                  value={recipeName}
                  handleRecipeNameChange={handleRecipeNameChange}
                />
                <RecipeIngredientsRow
                  type='text'
                  label='Ingredients'
                  value={recipeIngredients}
                  handleRecipeIngredientsChange={handleRecipeIngredientsChange}
                />
                <tr><td /><td /><td /><td><hr /></td></tr>
                <RecipeDirectionsRow
                  type='text'
                  label='Directions'
                  value={recipeDirections}
                  handleRecipeDirectionsChange={handleRecipeDirectionsChange}
                />
              </tbody>
            </table>
          </td>
          <td>&nbsp;</td><td>&nbsp;</td>
          <td>
            <div className='recipe'>
              <h1>{recipeName}</h1>
              <h4>Ingredients</h4>
              {recipeIngredients}<br /><br />
              <h4>Directions</h4>
              {recipeDirections}<br /><br />
            </div>
          </td>
        </tr>
      </tbody>
    </table>
  )
}
